@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
:root{font-size:16px}body{margin:0;font-family:'Press Start 2P', -apple-system, BlinkMacSystemFont, cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#6f42c1;word-spacing:-.4em}body *{box-sizing:border-box}html,body{height:100%}button,input,option,select{font-family:'Press Start 2P', -apple-system, BlinkMacSystemFont, sans-serif}a{color:#7217F4}.btn{margin:0 1em;padding:.75em 1em;border:4px solid #000;background-color:#45158a;cursor:pointer;color:#fff;text-decoration:none}#root{max-width:1200px;margin:0 auto;padding:2em}header,footer{margin-bottom:2em;padding:1em 2.5em;background-color:#fffa65;border:5px solid #e5df40}.card__name{width:100%;margin:0;max-width:calc(100% - 168px);font-size:1rem;line-height:1.6;text-transform:uppercase;word-wrap:anywhere}.card__price{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;max-width:168px}.card__price p{margin:0 0 0 .5em}.card__ghst{width:32px;min-width:32px;height:32px;min-height:32px}.card__data{padding:1em;background-color:#6f42c1}.card--listing .card__header{padding:.65em .75rem;background:#000}.card--listing .card__title{margin:0;font-size:.9rem;color:#fff;line-height:1.5}.card--listing .card__title small{font-size:65%}.card--listing .card__rarity{margin:.8em 0 0;color:#dccef0;font-size:.7rem;letter-spacing:.6px}.card--filtering .card__header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:.75em 1em}.card--portal{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;padding:1em}.card--portal .card__price{margin-top:1em}.card__svg{width:180px}table{width:100%;border-collapse:collapse;overflow:hidden;background-color:#fff;border:7px solid black;border-top-width:0;text-align:left}th,td{padding:1em calc(.75rem - .4rem);color:#1E1E1E;font-size:0.8rem}th{border-bottom:6px solid #000;text-align:left}tr{background-color:#fff;border-bottom:4px solid #000;transition:background-color .35s}tbody tr:hover{background-color:rgba(205,198,198,0.6)}

header h1{margin:0;font-size:1.5rem;text-align:center}

.intro{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between;padding:2em;border:15px solid #ffc107;background-color:#17a2b8}.intro__text{width:100%;margin:0 0 1.5rem;text-align:center;line-height:2}.intro__box{width:47.5%;padding:1.5em 1em;background-color:#fff;border:7px solid black}.intro__box h2{margin:0 0 1.2em;font-size:1.2rem}.intro__box p{margin:0 0 1.25em;font-size:0.75rem;line-height:1.85}.intro__box p:last-of-type{margin-bottom:0}

.options{margin-bottom:2em;border:5px solid black}.options__form{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:.75em 1em;background-image:linear-gradient(180deg, #722CF0 0%, #FA34F3 100%);border:5px solid #ffc107}.form__label{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:.9rem;color:#fff}.form__label p{margin:0}.form__input{margin-left:.35em;padding:.65em .3em;border:solid 4px #000}.form__input[type="number"]{width:80px}.form__input--submit{margin:0;padding:.65em 1em;cursor:pointer}

.listings{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr;grid-gap:2em;gap:2em;grid-template-areas:'.';padding:1.5em;border:15px solid #ffc107;background-color:#17a2b8}.listings--3{grid-template-columns:1fr 1fr}.listings--none{display:block}.listings__warning{margin:0;text-align:center;line-height:2}.listings__loading{margin:0;font-size:2rem;text-align:center}

.filtering{padding:2em;background-color:#17a2b8;border:15px solid #ffc107;text-align:center}.filtering h3{margin-top:0}.filtering p{max-width:840px;margin:1em auto 0;line-height:2}.filtering__card{background-color:#fff;border:7px solid black;text-decoration:none;color:#000}.filtering__card p{font-size:.85rem;text-transform:uppercase}.data{display:-webkit-flex;display:flex;margin-bottom:1.25em}.data:last-of-type{margin-bottom:0}.data p{margin:0;color:#fff}.data .value{margin-left:.35em}

.views{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin-bottom:2em}

footer{margin:2em 0 0;text-align:center}footer p{margin:0;font-size:.75rem}footer p:first-of-type{margin-bottom:0.85em}

